@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans+Arabic:wght@200;400;700&family=DM+Serif+Display&display=swap);
*,*::before,*::after{box-sizing:border-box;padding:0;margin:0;list-style-type:none;font-family:'IBM Plex Sans Arabic', sans-serif;font-weight:700}a{text-decoration:none;color:black}body{background-color:#fcfcfc}.App{display:flex;flex-direction:column;min-height:100vh}.pricing{display:flex;justify-content:center;grid-gap:40px;gap:40px;padding-top:120px;padding-bottom:40px;background-color:#fcfcfc}@media screen and (max-width: 800px){.pricing{flex-direction:column;align-items:center;padding-top:80px}}.features{display:flex;justify-content:space-around;flex-wrap:wrap;grid-gap:5vw;gap:5vw;max-width:1400px;padding:60px 20px;margin:0 auto}@media screen and (min-width: 1400px){.features{grid-gap:80px;gap:80px;padding:60px 40px}}.footer-filler{flex:1 1}footer{display:flex;justify-content:space-around;padding:16px;background-color:#f0f0f0}footer p{font-weight:400}@media screen and (max-width: 600px){footer{flex-direction:column;align-items:center}}.me{align-self:center;font-size:1.1rem}

.navbar{top:0;position:sticky;height:80px;z-index:5;background:#280253;font-size:1.2rem}.navbar-container{display:flex;justify-content:space-between;margin-right:auto;margin-left:auto;padding-right:25px;padding-left:25px;width:100%;max-width:1600px;height:80px}.navbar-logo{display:flex;align-items:center;grid-gap:5px;gap:5px;cursor:pointer;font-family:'DM Serif Display', serif;font-weight:700px;font-size:2.3rem;color:white;text-decoration:none}.nav-menu{display:flex;list-style:none}@media screen and (max-width: 620px){.nav-menu{display:flex;flex-direction:column;position:absolute;top:80px;left:-100%;width:100%;height:90vh;transition:all 0.5s ease}.nav-menu.active{background:#11052C;left:0;transition:all 0.6s ease;z-index:1}}.nav-menu-item{height:80px}@media screen and (max-width: 620px){.nav-menu-item{width:100%;border-bottom:#fcfcfc 1px solid}}.nav-menu-item:hover{background:#F43B86}.nav-menu-link{display:flex;align-items:center;padding:0.5rem 1rem;height:100%;font-size:1.5rem;color:#fcfcfc;text-decoration:none}@media screen and (max-width: 620px){.nav-menu-link{padding:2rem;padding-left:3rem;width:80%;text-align:center;font-size:1.5rem;font-weight:500}.nav-menu-link:hover{-webkit-transform:scale(1.2) translateX(45px);transform:scale(1.2) translateX(45px);transition:all 0.3s ease}}.menu-icon{display:none}@media screen and (max-width: 620px){.menu-icon{display:block;position:absolute;top:23px;right:10px;cursor:pointer}}

.main-card-container,.main-card-container--reverse{display:flex;flex-direction:column;align-items:center;padding-top:40px;background-color:#fff}.main-card-container--reverse{background-color:#3D087B}.main-card-container--reverse p{color:#FFF}.main-card-media,.main-card-media--reverse{display:flex;grid-gap:20px;gap:20px;padding-bottom:10px}.main-card-media img,.main-card-media--reverse img{display:block;width:45vw;max-width:600px;margin-left:auto}.main-card-media div,.main-card-media--reverse div{width:45vw;max-width:700px;margin-right:auto;font-size:1.5rem}.main-card-media--reverse{flex-direction:row-reverse}@media screen and (max-width: 620px){.main-card-media,.main-card-media--reverse{flex-direction:column-reverse}.main-card-media img,.main-card-media--reverse img{width:90vw}.main-card-media div,.main-card-media--reverse div{width:90vw;font-size:1.2rem}}.main-card-heading{padding-bottom:30px;font-size:3.4rem;color:#F43B86}@media screen and (max-width: 600px){.main-card-heading h1{font-size:2.4rem}}@media screen and (min-width: 1200px){.main-card-heading h1{font-size:4rem}}

.pricing-card-container{display:flex;flex-direction:column;align-items:center;border-radius:6px;padding:20px;background:#5b2be0;color:white}.pricing-card-container:hover{-webkit-transform:scale(1.2);transform:scale(1.2);transition:all 0.3s ease;cursor:pointer}@media screen and (min-width: 1200px){.pricing-card-container{margin:40px;-webkit-transform:scale(1.3);transform:scale(1.3)}.pricing-card-container:hover{-webkit-transform:scale(1.5);transform:scale(1.5);transition:all 0.3s ease}}@media screen and (max-width: 800px){.pricing-card-container{margin:40px;-webkit-transform:scale(1.2);transform:scale(1.2)}.pricing-card-container:hover{-webkit-transform:scale(1.3);transform:scale(1.3);transition:all 0.3s ease}}.pricing-card-tier{padding-top:10px;font-style:"Kaisei HarunoUmi, serif";font-size:2.5rem;font-weight:400}.pricing-card-price{font-size:3rem;color:#fbff00;font-weight:700}.pricing-card-features{padding-top:30px;padding-bottom:40px;font-size:1.2rem}.pricing-card-features li{font-weight:200}

